.App {
    text-align: center;
}

.App-header {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
.App-title{
    font-family: Montserrat, sans-serif;
    font-size: calc(58px - 2vmin);
}

body, html, #root{
    background-color: #121212;
    box-sizing: border-box;
}
